* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
}

html {
    font-size: 62.5%;
}

body {
    background: var(--white);
    font-family: var(--font-family);
    min-height: 100vh;
    color: var(--text-color);
}

ul,
ol {
    list-style: none;
}

a {
    color: inherit;
    text-decoration: inherit;
}

button {
    outline: 0;
    background: none;
    border: none;
    cursor: pointer;
}

#root {
    position: relative;
}
/* width */
::-webkit-scrollbar {
    width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}



:root {
    --white: #FFFFFF;
    --primary-color: #8688BC;
    --text-color: #424347;
    --text-helper-color: #BBBBBB;
    --text-action-color: #3E3E3E;

    --soft-grey: #F3F3F3;
}
:root {
    --font-family: 'Roboto', sans-serif;
    --font-size: 1.6rem;
    --font-weight: 500;

    /* Title */
    --title-font-size: 4rem;
    --title-font-weight: 700;

    /* Helper */
    --helper-font-size: 1.3rem;
}
:root {
    --padding-box: 2.5rem;
    --box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.1);
}




